import { destinations } from "../../data/destinations";
import Image from "next/image";
import Link from "next/link";

interface PageProps {
  params: Promise<{ id: string }>;
}

export default async function DestinationDetail({ params }: PageProps) {
  const { id } = await params;
  const destination = destinations.find((d) => d.id === id);

  if (!destination) {
    return (
      <main className="flex h-screen items-center justify-center bg-gradient-to-b from-white via-sky-50 to-sky-100 text-gray-600">
        Destination not found.
      </main>
    );
  }

  // Related destinations
  const related = destinations
    .filter((d) => d.id !== id)
    .slice(0, 3);

  return (
    <main className="min-h-screen bg-gradient-to-b from-white via-sky-50 to-white text-gray-800">
      {/* Hero Section */}
      <section className="relative w-full h-[65vh] overflow-hidden">
        <Image
          src={destination.image}
          alt={destination.cityName}
          fill
          priority
          className="object-cover scale-105 hover:scale-110 transition-transform duration-[2000ms] ease-out"
        />
        <div className="absolute inset-0 bg-gradient-to-t from-white via-white/30 to-transparent" />
        <div className="absolute bottom-12 left-6 md:left-16">
          <h1 className="text-5xl md:text-6xl font-extrabold text-transparent bg-clip-text bg-gradient-to-r from-sky-700 via-sky-600 to-sky-400 drop-shadow-[0_2px_10px_rgba(0,0,0,0.15)]">
            {destination.cityName}
          </h1>
          <p className="text-sky-700/80 text-lg mt-2 font-medium">
            Discover the hidden beauty and timeless charm
          </p>
        </div>
      </section>

      {/* Main Content */}
      <section className="relative -mt-16 z-10">
        <div className="max-w-5xl mx-auto bg-white/80 backdrop-blur-xl border border-sky-100 rounded-3xl shadow-[0_0_25px_rgba(0,0,0,0.05)] p-10 md:p-14 space-y-8">
          <p className="text-gray-700 text-lg leading-relaxed tracking-wide">
            {destination.description}
          </p>

          {/* Metadata */}
          <div className="flex flex-wrap justify-between items-center text-sm text-gray-500 border-t border-sky-100 pt-6">
            <span>
              ‚úçÔ∏è Author:{" "}
              <span className="text-sky-700 font-semibold">
                {destination.author}
              </span>
            </span>
            <span>
              üìÖ Created:{" "}
              <span className="text-sky-700 font-semibold">
                {destination.createdAt}
              </span>
            </span>
            {destination.updatedAt && (
              <span>
                üîÅ Last Modified:{" "}
                <span className="text-sky-700 font-semibold">
                  {destination.updatedAt}
                </span>
              </span>
            )}
          </div>

          {/* CTA */}
          <div className="flex justify-center pt-6">
            <button className="px-10 py-3 bg-gradient-to-r from-sky-600 to-sky-500 text-white font-semibold text-lg rounded-full shadow-md hover:shadow-lg hover:from-sky-700 hover:to-sky-600 transition-all duration-400">
              Plan Your Trip
            </button>
          </div>
        </div>
      </section>

      {/* Related Destinations */}
      <section className="max-w-6xl mx-auto px-6 py-20">
        <h2 className="text-3xl font-bold text-sky-800 mb-10">
          Related Destinations
        </h2>
        <div className="grid gap-10 sm:grid-cols-2 lg:grid-cols-3">
          {related.map((d) => (
            <Link
              href={`/destinations/${d.id}`}
              key={d.id}
              className="group rounded-2xl overflow-hidden shadow-md bg-white border border-sky-100 hover:shadow-xl hover:-translate-y-1 transition-all duration-500"
            >
              <div className="relative h-56 overflow-hidden">
                <Image
                  src={d.image}
                  alt={d.cityName}
                  fill
                  className="object-cover group-hover:scale-110 transition-transform duration-700 ease-out"
                />
              </div>
              <div className="p-6 space-y-3">
                <h3 className="text-xl font-semibold text-sky-800 group-hover:text-sky-600 transition-colors">
                  {d.cityName}
                </h3>
                <p className="text-gray-600 text-sm leading-relaxed line-clamp-3">
                  {d.description}
                </p>
              </div>
            </Link>
          ))}
        </div>
      </section>
    </main>
  );
}
